.profile {
  width: 300px;
  height: auto;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  border: rgba(220, 209, 223, 0.42) 2px solid;
}

.avatar {
  border-radius: 50%;
  margin-top: 30px;
}

.name {
  font-size: 20px;
  font-weight: 700;
  color: rgb(31, 51, 73);
}

.tag,
.location {
  font-size: 16px;
  color: rgb(118, 134, 150);
}

.stats {
  display: flex;
  padding: 0;
  list-style: none;
  width: 100%;
  margin: 0;
}

.stats li {
  display: flex;
  flex-direction: column;
  width: 33.33%;
  border: rgba(220, 209, 223, 0.42) 1px solid;
  background-color: rgb(229, 237, 238);
}

.stats span {
  margin: 10px;
}

.label {
  color: rgb(118, 134, 150);
  font-size: 14px;
}

.quantity {
  color: rgb(31, 51, 73);
  font-size: 14px;
  font-weight: 700;
}
