.friendList {
  list-style: none;
  padding: 0;
  margin-left: 345px;
  margin-right: 345px;
}
.item:hover {
  background-color: aqua;
}
.item {
  padding: 10px;
  display: flex;
  align-items: center;
  border: #b8bbbd 1px solid;
  box-shadow: rgb(0, 0, 0, 0.3) 0.5px 1px 1px;
  width: 270px;
  margin-bottom: 10px;
}

.avatar {
  border-radius: 10px;
}

.name {
  padding-left: 10px;
  font-size: 20px;
  font-weight: bold;
}
span {
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-right: 20px;
}
.onLine {
  background-color: green;
}

.offLine {
  background-color: red;
}
